import React, { Component } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { MapControls } from 'three/examples/jsm/controls/MapControls.js';
import { TessellateModifier } from 'three/examples/jsm/modifiers/TessellateModifier.js';
const TWEEN = require('@tweenjs/tween.js');
THREE = window.THREE = require('three');
require('three/examples/js/loaders/GLTFLoader');


class ShadowTest extends Component {
	state = {
		rotate: false,
		pressed: false,
		distorted: false,
		checkItersect: true
	}

	start = () => {
		if (!this.frameId) {
			this.frameId = requestAnimationFrame(this.animate)
		}
	}


	onMouseMove = (event) => {
		this.mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
		this.mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
	}

	sceneSetup = () => {
		this.scene = new THREE.Scene();
		this.scene.background = new THREE.Color( "rgba(255, 255, 255, 0.9)" );
		this.renderer = new THREE.WebGLRenderer();
		this.renderer.shadowMap.enabled = true;
		this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
		this.renderer.setPixelRatio( window.devicePixelRatio );
		this.renderer.setSize( window.innerWidth, window.innerHeight );

		this.mount.appendChild(this.renderer.domElement);

		this.camera = new THREE.PerspectiveCamera( 90, window.innerWidth / window.innerHeight, 1, 1000 );
		this.camera.position.set(0, 100, 100);
		this.controls = new OrbitControls( this.camera, this.renderer.domElement );
		// this.controls.enableZoom = false;
		this.controls.target = new THREE.Vector3(0, 0, 99);
		this.controls.update();

		window.addEventListener('resize', () => {
			this.fitCameraToObject(this.camera, this.controls, [this.mainText, this.portfolio], window.innerWidth > 1000 ? 1.2 : 1.3);
			this.renderer.setSize(window.innerWidth, window.innerHeight);
			this.camera.aspect = window.innerWidth / window.innerHeight;
			this.camera.updateProjectionMatrix();
		});

		this.mouse = new THREE.Vector2();
		this.intersected = null;
		this.raycaster = new THREE.Raycaster();

		this.uniforms = {
			amplitude: { value: 0.0 }
		};
		this.counter = 0;

		window.addEventListener('mousemove', this.onMouseMove, false);

		this.objectsIncluded = [];
		this.objectsToShake = [];
	}





	componentDidMount() {
		this.sceneSetup();

		const objectLoader = new THREE.GLTFLoader();
			objectLoader.load('models/frank-2.glb', gltf => {

			gltf.scene.traverse( function( node ) {

	        if ( node instanceof THREE.Mesh ) { node.castShadow = true; }

	    } );
			console.log(gltf)
			this.gltf = gltf.scene;
			this.gltf.scale.set(0.1, 0.1, 0.1);
			this.gltf.position.set(0, 0, 0);
			this.gltf.castShadow = true;
			this.gltf.receiveShadow = true;


			this.scene.add(this.gltf);
		})


		// var sphereGeometry = new THREE.SphereBufferGeometry( 15, 32, 32 );
		// var sphereMaterial = new THREE.MeshStandardMaterial( { color: 0xff0000 } );
		// var sphere = new THREE.Mesh( sphereGeometry, sphereMaterial );
		// sphere.castShadow = true; //default is false
		// sphere.receiveShadow = false; //default
		// this.scene.add( sphere );

		var spotLight = new THREE.SpotLight( 0xffffff );
		spotLight.position.set( 0, 100, 0 );
		spotLight.castShadow = true;


		spotLight.shadow.mapSize.width = 1024;
		spotLight.shadow.mapSize.height = 1024;

		spotLight.shadow.mapSize.width = 512;  // default
		spotLight.shadow.mapSize.height = 512; // default
		spotLight.shadow.camera.near = 0.5;       // default
		spotLight.shadow.camera.far = 500
		spotLight.power = 100;
		// spotLight.target = sphere;

		this.scene.add( spotLight );



		var planeGeometry = new THREE.PlaneBufferGeometry( 200, 200, 32, 32 );
		var planeMaterial = new THREE.MeshStandardMaterial( { } )
		var plane = new THREE.Mesh( planeGeometry, planeMaterial );
		plane.rotation.x = - Math.PI / 2.0;
		plane.receiveShadow = true;
		plane.position.y = -50;
		this.scene.add( plane );


		// const directionalLight = new THREE.DirectionalLight( 0xffffff, 0.5 );
		// directionalLight.castShadow = true;
		// directionalLight.position.set( 0, 1, 1 );
		// directionalLight.position.multiplyScalar( 5 );
		// directionalLight.shadow.mapSize.width = 2048;
		// directionalLight.shadow.mapSize.height = 2048;
		// this.scene.add(directionalLight);

		const textureLoader = new THREE.TextureLoader();
		const floorMaterial = new THREE.MeshStandardMaterial({});


		const floorGeometry = new THREE.PlaneBufferGeometry( 40, 40 );
		const floorMesh = new THREE.Mesh( floorGeometry, floorMaterial );
		floorMesh.receiveShadow = true;
		floorMesh.rotation.x = - Math.PI / 2.0;
		floorMesh.position.y = -50;
		this.scene.add(floorMesh);

		this.start()

	}




	animate = () => {
		this.frameId = window.requestAnimationFrame(this.animate);
		this.renderer.render( this.scene, this.camera );
		TWEEN.update()
	}


	render() {
		return (
			<div ref={(mount) => { this.mount = mount }} className="canvas" >
			</div>
		)
	}
}

export default ShadowTest;
