import React, { Component, useState } from 'react';
import { NavLink, Link, withRouter } from 'react-router-dom';
import {useSpring, animated, useTransition, useTrail} from 'react-spring'
import Work from './Work'

const Show = (state) => {
	const {works} = state;
  const props = useSpring({
		marginTop: '0',
		from : {marginTop: '100%'}
  })
	return (
		<animated.section style={props}>
			{works.map(item =>
				<Work key={item.id} id={item.id} img={item.img} link={item.link} tech={item.tech} github={item.github} />
			)}
		</animated.section>
	)
}


class Works extends Component {
	state = {
		bool: false,
		works: [
			{
				id: 1,
				img: ['img/rybieudko.png'],
				link: 'https://rybieudko.pl',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS',
					'Bootstrap',
					'MySQL'
				]
			},
			{
				id: 2,
				img: ['img/chatmain.png', 'img/chatchat.png'],
				link: 'https://xsphrx.github.io/chatapp/',
				github: 'https://github.com/xsphrx/chatapp',
				tech: [
					'React',
					'Redux',
					'Firebase',
					'Materialize CSS',
					'PWA'
				]
			},
			{
				id: 3,
				img: ['img/mxe.png'],
				link: 'https://mxe2025.com/shop/',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS'
				]
			},
			{
				id: 4,
				img: ['img/skupkomiksow.png'],
				link: 'https://www.skupkomiksow.pl/',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS'
				]
			},
			{
				id: 5,
				img: ['img/zdroweziolo.png'],
				link: 'https://zdroweziolo.pl/',
				github: '',
				tech: [
					'Django',
					'JS',
					'CSS'
				]
			}
		]
	}

	render() {
		return (
			<div className="container-fluid">
				<div className="row">
					<div className="col-1"></div>
					<div className="col-10">
						<div>
							<div className="text-center">
								<div className="goBackButton" onClick={() => this.props.history.push('/')}>
									<div className="goBackText">Go Back</div>
								</div>
									<Show works={this.state.works} />
								</div>
								<div className="mt-5"></div>
							</div>
					</div>
					<div className="col-1"></div>
				</div>
			</div>
		)
	}
}

export default withRouter(Works);
