import React, { Component, useState } from 'react';
import { Redirect } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import {useSpring, animated, useTransition} from 'react-spring'

const Technology = (state) => {
	const {tech} = state;
	const props = useSpring({
	  transform: 'translate3d(0,0,0)',
		from : {transform: 'translate3d(500px,0,0)'}
	})
	return (
		<animated.div style={props} className="mt-5">
			<div className="techdiv1">
				<h2>Technology</h2>
				<div className="techdiv2">
					{tech.map((item, i) =>
						<div className="tech" key={i} >
							{item}
						</div>
					)}
				</div>
			</div>
		</animated.div>
	)
}

const Images = (state) => {
	const {img} = state;
	const props = useSpring({
	  transform: 'translate3d(0,0,0)',
		from : {transform: 'translate3d(-500px,0,0)'}
	})
	return (
		<animated.div style={props} className="my-5">
			<div className="techdiv1">
				<h2>Images</h2>
				{img.map((img, i) =>
					<div className="img-container text-center" key={i}>
						<img src={img} alt="" style={{maxWidth: '100%', border: '1px solid grey', width: '800px', boxShadow: '0 10px 20px rgba(0,0,0,0.15)'}}/>
					</div>
				)}
			</div>
		</animated.div>
	)
}

const GoToWebsite = (state) => {
	const {link} = state;
	const props = useSpring({
		transform: 'translate3d(0,0px,0)',
		from : {transform: 'translate3d(0,-100px,0)'}
	})
	return (
		<animated.div style={props}>
			<div className="goBackButton text-center">
				<div onClick={() => window.open(link)} className="goBackText">Go To Website</div>
			</div>
		</animated.div>
	)
}

class Project extends Component {
	state = {
		works: [
			{
				id: 1,
				img: ['img/rybieudko.png'],
				link: 'https://rybieudko.pl',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS',
					'Bootstrap',
					'MySQL'
				]
			},
			{
				id: 2,
				img: ['img/chatmain.png', 'img/chatchat.png'],
				link: 'https://xsphrx.github.io/chatapp/',
				github: 'https://github.com/xsphrx/chatapp',
				tech: [
					'React',
					'Redux',
					'Firebase',
					'Materialize CSS',
					'PWA'
				]
			},
			{
				id: 3,
				img: ['img/mxe.png'],
				link: 'https://mxe2025.com/shop/',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS'
				]
			},
			{
				id: 4,
				img: ['img/skupkomiksow.png'],
				link: 'https://www.skupkomiksow.pl/',
				github: '',
				tech: [
					'Flask',
					'JS',
					'CSS'
				]
			},
			{
				id: 5,
				img: ['img/zdroweziolo.png'],
				link: 'https://zdroweziolo.pl/',
				github: '',
				tech: [
					'Django',
					'JS',
					'CSS'
				]
			}
		],
		work: {
			id: null,
			img: [],
			link: '',
			github: '',
			tech: []
		}
	}

	componentDidMount() {
		this.setState({work: this.state.works.find(x => x.id == this.props.match.params.id)})
	}

	render() {
		return (
			<div className="container-fluid">
				<div className="row">
					<div className="col-1"></div>
					<div className="col-10">
						<GoToWebsite link={this.state.work.link} />
						<Technology tech={this.state.work.tech} />
						<Images img={this.state.work.img} />
					</div>
					<div className="col-1"></div>
				</div>
			</div>
		)
	}
}

export default Project;
