import React, { Component, useState } from 'react';
import { Redirect } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import ThreeBackground from './ThreeBackground';
import ThreeTable from './ThreeTable';
import WaveTest from './WaveTest'
import TesselationTest from './TesselationTest'
import ShadowTest from './ShadowTest'
import Works from './Works'
import {useSpring, animated, useTransition} from 'react-spring'

const Show = (state) => {
	const {showTable} = state;
	const [toggle, setMessage] = useState(false)
  const transitions = useTransition(toggle, null, {
    from: { marginTop: '100%' },
    enter: { marginTop: '0' },
    leave: { marginTop: '100%' },
  })
	return transitions.map(({ item, key, props }) =>
     <animated.section key={key} style={props}>
		 	<Works showTable={showTable} />
		 </animated.section>
  )
}

class Home extends Component {
	state = {
		table: true
	}


	render() {
		return (
			<div>
				{this.state.table ?
					<ThreeTable />
					:
					<div className="container-fluid">
						<div className="row">
							<div className="col-1"></div>
							<div className="col-10">
								<Show showTable={() => this.setState({table: true})} />
							</div>
							<div className="col-1"></div>
						</div>
					</div>
				}
			</div>
		)
	}
}

export default Home;
