import React, { Component, useState } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { MapControls } from 'three/examples/jsm/controls/MapControls.js';
import { TessellateModifier } from 'three/examples/jsm/modifiers/TessellateModifier.js';
import {useSpring, animated, useTransition} from 'react-spring'
import { NavLink, Link, withRouter } from 'react-router-dom';
const TWEEN = require('@tweenjs/tween.js');
THREE = window.THREE = require('three');
require('three/examples/js/loaders/GLTFLoader');


class ThreeTable extends Component {
	state = {
		rotate: false,
		pressed: false,
		distorted: false,
		checkItersect: true,
		loaded: false,
		showReset: false
	}

	start = () => {
		if (!this.frameId) {
			this.frameId = requestAnimationFrame(this.animate)
		}
	}

	handleScroll = (e) => {
		// this.camera.lookAt (new THREE.Vector3 (111, 0, 0));
		if (e.deltaY > 0){
			this.animateVector3(this.controls.target, new THREE.Vector3(0, 100, 0), {
				duration: 1000,
				easing : TWEEN.Easing.Quadratic.InOut,
				runOnUpdate: this.controls.update
			});
		}
	}

	onClick = () => {
		this.raycaster.setFromCamera(this.mouse, this.camera);
		const intersects = this.raycaster.intersectObjects(this.scene.children);
		if(intersects.length > 0 && this.containsObject(intersects[0].object)) {
			intersects[0].object.onClick();
		}
	}

	animateVector3 = (vectorToAnimate, target, options) => {

			options = options || {};
			// get targets from options or set to defaults
			var to = target || THREE.Vector3(),
					easing = options.easing || TWEEN.Easing.Quadratic.In,
					duration = options.duration || 2000;
			// create the tween
			var that = this;
			var tweenVector3 = new TWEEN.Tween(vectorToAnimate)
					.to({ x: to.x, y: to.y, z: to.z, }, duration)
					.easing(easing)
					.onUpdate(function(d) {
							if(options.update){
									options.update(d);
							}
							if(options.runOnUpdate){
								options.runOnUpdate()
							}
					 })
					.onComplete(function(){
						if(options.callback) options.callback();
					});
			// start the tween
			tweenVector3.start();
			// return the tween in case we want to manipulate it later on
			return tweenVector3;
	}

	fitCameraToObject = (camera, controls, selection, fitOffset = 1) => {

		const box = new THREE.Box3();

		for( const object of selection ) box.expandByObject( object );

		const size = box.getSize( new THREE.Vector3() );
		const center = box.getCenter( new THREE.Vector3() );

		const maxSize = Math.max( size.x, size.y, size.z );
		const fitHeightDistance = maxSize / ( 2 * Math.atan( Math.PI * camera.fov / 360 ) );
		const fitWidthDistance = fitHeightDistance / camera.aspect;
		const distance = fitOffset * Math.max( fitHeightDistance, fitWidthDistance );

		const direction = controls.target.clone()
			.sub( camera.position )
			.normalize()
			.multiplyScalar( distance );

		controls.maxDistance = distance * 10;
		controls.target.copy(center);

		camera.near = distance / 100;
		camera.far = distance * 100;
		camera.updateProjectionMatrix();

		camera.position.copy(controls.target).sub(direction);

		controls.update();

	}

	containsObject = (obj) => {
		for(const x in this.objectsIncluded) {
			if(this.objectsIncluded.hasOwnProperty(x) && this.objectsIncluded[x] === obj){
				return true;
			}
		}
		return false;
	}

	checkIfIntersects = () => {
		this.raycaster.setFromCamera(this.mouse, this.camera);
		const intersects = this.raycaster.intersectObjects(this.scene.children);
		if(intersects.length > 0) {
			if(this.intersected !== intersects[0].object) {
				if(this.intersected && this.containsObject(this.intersected)) {
					this.intersected.stopHover();
				}
				this.intersected = intersects[0].object;
				if(this.containsObject(this.intersected)){
					this.intersected.hover();
				}
			}
		} else {
			if(this.intersected && this.containsObject(this.intersected)){
				this.intersected.stopHover();
			}
			this.intersected = null;
		}
	}

	onMouseMove = (event) => {
		this.mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
		this.mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
		this.raycaster.setFromCamera(this.mouse, this.camera);
		this.raycaster.ray.intersectPlane(this.plane, this.pointOfIntersection);
  	this.box.lookAt(this.pointOfIntersection);
		this.gltf.lookAt(this.pointOfIntersection)
	}

	sceneSetup = () => {
		this.scene = new THREE.Scene();
		this.scene.background = new THREE.Color( "rgba(254, 254, 254)" );
		this.renderer = new THREE.WebGLRenderer({ antialias: true });
		this.renderer.shadowMap.enabled = true;
		this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
		this.renderer.setPixelRatio( window.devicePixelRatio );
		this.renderer.setSize( window.innerWidth, window.innerHeight );

		this.mount.appendChild(this.renderer.domElement);

		this.camera = new THREE.PerspectiveCamera( 90, window.innerWidth / window.innerHeight, 1, 1000 );
		this.camera.position.set(0, 100, 100);
		this.controls = new MapControls( this.camera, this.renderer.domElement );
		this.controls.enabled = false;
		this.controls.enableZoom = false;
		this.controls.target = new THREE.Vector3(0, 0, 99);
		this.controls.update();

		window.addEventListener('resize', () => {
			this.fitCameraToObject(this.camera, this.controls, [this.mainText, this.portfolio], window.innerWidth > 1000 ? 1.2 : 1.3);
			this.renderer.setSize(window.innerWidth, window.innerHeight);
			this.camera.aspect = window.innerWidth / window.innerHeight;
			this.camera.updateProjectionMatrix();
		});

		this.mouse = new THREE.Vector2();
		this.intersected = null;
		this.raycaster = new THREE.Raycaster();

		this.uniforms = {
			amplitude: { value: 0.0 }
		};
		this.counter = 0;


		this.objectsIncluded = [];
		this.objectsToShake = [];
	}

	lightSetup = () => {
		let light;
		light = new THREE.AmbientLight(0x404040, 2);
		this.scene.add(light);
		light = new THREE.DirectionalLight(0xffffff, 0.5);
		this.scene.add(light);
	}

	button = (obj, background) => {
		const position = obj.position;
		const size = new THREE.Box3().setFromObject(obj).getSize();
		const geometry = new THREE.PlaneGeometry(size.x*2+8, size.z*2+8, 2);
		const material = new THREE.MeshLambertMaterial({transparent: true, opacity: 0});
		const mesh = new THREE.Mesh(geometry, material);
		mesh.rotation.setFromVector3(new THREE.Vector3(Math.PI / -2, 0, 0));
		mesh.position.set(position.x, position.y+1, position.z);
		mesh.hover = () => {
			document.body.style.cursor = 'pointer';
			background.hover();
		}
		mesh.stopHover = () => {
			document.body.style.cursor = 'default';
			background.stopHover();
		}
		return mesh;
	}

	textBackground = (obj) => {
		const position = obj.position;
		const size = new THREE.Box3().setFromObject(obj).getSize();
		const geometry = new THREE.BoxGeometry(size.x*2+8, 0, 2);
		const material = new THREE.MeshLambertMaterial({color: 0x000000, transparent: true, opacity: 0});
		const mesh = new THREE.Mesh(geometry, material);
		mesh.rotation.setFromVector3(new THREE.Vector3(Math.PI / -2, 0, 0));
		mesh.position.set(position.x, position.y-1, position.z);
		mesh.hover = () => {
			mesh.material.opacity = 0.2;
			new TWEEN.Tween(mesh.scale)
			.to({ y: 20 }, 200)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();
		}
		mesh.stopHover = () => {
			new TWEEN.Tween(mesh.scale)
			.to({ y: 1 }, 200)
			.easing(TWEEN.Easing.Quadratic.Out)
			.onComplete(() => mesh.material.opacity = 0)
			.start();
		}
		return mesh;
	}

	text = (text, uniforms, font, size, posx, posy, posz) => {
		let geometry, mesh;

		geometry = new THREE.TextGeometry(text, {
			font: font,
			size: size,
			height: 0.01,
			curveSegments: 4,
			bevelThickness: 0.3,
			bevelSize: 0.01,
			bevelEnabled: true
		});
		geometry.center();


		const tessellateModifier = new TessellateModifier( 1 );

		// how many pieces is the string split into
		for (let i=0; i<6; i++) {
			tessellateModifier.modify(geometry);
		}

		geometry = new THREE.BufferGeometry().fromGeometry(geometry);

		const numFaces = geometry.attributes.position.count / 3;
		const displacement = new Float32Array( numFaces * 6 * 3 );
		for ( var f = 0; f < numFaces; f ++ ) {
			const index = 9 * f;
			const d = 111 * ( 0.5 - Math.random() );
			for ( var i = 0; i < 1111; i++ ) {
				displacement[ index + ( 3 * i ) ] = d;
				displacement[ index + ( 3 * i ) + 1 ] = d;
				displacement[ index + ( 3 * i ) + 2 ] = d;
			}
		}

		geometry.addAttribute('displacement', new THREE.BufferAttribute(displacement, 3));

		const shaderMaterial = new THREE.ShaderMaterial({
			uniforms: uniforms,
			vertexShader: document.getElementById('vertexshader').textContent,
			fragmentShader: document.getElementById('fragmentshader').textContent
		});

		mesh = new THREE.Mesh(geometry, shaderMaterial);
		mesh.rotation.setFromVector3(new THREE.Vector3(Math.PI / -2, 0, 0));
		mesh.position.set(posx, posy, posz);

		mesh.shake = () => {
			mesh.position.set(posx+Math.random()*10-5, posy+Math.random()*10-5, posz+Math.random()*10-5);
			mesh.material.uniforms.amplitude.value += 0.001;
		}

		mesh.rotate = () => {
			mesh.rotation.y += 0.001;
			mesh.rotation.x += 0.001;
			mesh.rotation.z += 0.001;
		}

		mesh.reset = () => {
			new TWEEN.Tween(mesh.position)
			.to({x: posx, y: posy, z: posz}, 5000)
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();
			new TWEEN.Tween(mesh.rotation)
			.to({x:Math.PI / -2, y: 0, z: 0})
			.easing(TWEEN.Easing.Quadratic.Out)
			.start();
		}

		mesh.resetQuick = () => {
			mesh.position.set(posx, posy, posz);
		}

		return mesh;
	}

	textSetup = () => {
		new THREE.FontLoader().load('fonts/helvetiker_bold.typeface.json', font => {
			this.mainText = this.text("Hi, I'm a full-stack developer.", this.uniforms, font, 10, 0, 0, 0);
			this.scene.add(this.mainText);
			this.objectsToShake.push(this.mainText);

			this.portfolio = this.text('Work', this.uniforms, font, 7, -80, 0, 250);
			this.portfolioBackground = this.textBackground(this.portfolio);
			this.portfolioButton = this.button(this.portfolio, this.portfolioBackground);
			this.portfolioButton.onClick = () => {
				document.body.style.cursor = 'default';
				this.props.history.push('/works');
			}
			this.scene.add(this.portfolio);
			this.scene.add(this.portfolioBackground);
			this.scene.add(this.portfolioButton);
			this.objectsIncluded.push(this.portfolioButton);
			this.objectsToShake.push(this.portfolio);

			this.CV = this.text('Resume', this.uniforms, font, 7, 80, 0, 250);
			this.CVBackground = this.textBackground(this.CV);
			this.CVButton = this.button(this.CV, this.CVBackground);
			this.CVButton.onClick = () => {
				window.open('/portfolio/Przemyslaw_Maranowski_CV.pdf')
			}
			this.scene.add(this.CV);
			this.scene.add(this.CVBackground);
			this.scene.add(this.CVButton);
			this.objectsIncluded.push(this.CVButton);
			this.objectsToShake.push(this.CV);

			this.fitCameraToObject(this.camera, this.controls, [this.mainText, this.portfolio, this.CV], window.innerWidth > 1000 ? 1.2 : 1.3);
		});
	}

	loadFrank = () => {
		const objectLoader = new THREE.GLTFLoader();
		objectLoader.load(
			'models/frank-2.glb',
			gltf => {

				gltf.scene.traverse(node => {
	      	if ( node instanceof THREE.Mesh ) { node.castShadow = true; }
		    });

				this.gltf = gltf.scene;
				this.gltf.scale.set(0.15, 0.15, 0.15);
				this.gltf.up = new THREE.Vector3(0, 0, -1);
				this.gltf.position.set(0, 0, 130);
				this.gltf.rotation.set(Math.PI / -2, 0, 0);
				this.gltf.castShadow = true;
				this.gltf.receiveShadow = true;

				new TWEEN.Tween(this.gltf.position)
				.to({y: 15}, 3000)
				.yoyo(true)
				.repeat(Infinity)
				.easing(TWEEN.Easing.Quadratic.Out)
				.start();


				this.gltf.shake = () => {
					this.gltf.position.set(Math.random()*20-5, Math.random()*20-5, 130+Math.random()*20-5);
					// this.gltf.scale.x -= 0.0013;
				}

				this.gltf.reset = () => {
					new TWEEN.Tween(this.gltf.scale)
					.to({z: 0.15, x: 0.15, y: 0.15}, 2000)
					.start();
				}

				this.gltf.resetLookAt = () => {
					const startRotation = new THREE.Euler().copy(this.gltf.rotation)
					this.gltf.lookAt(this.camera.position)
					const endRotation = new THREE.Euler().copy(this.gltf.rotation)
					this.gltf.rotation.copy(startRotation)
					new TWEEN.Tween(this.gltf.rotation)
					.to({x: endRotation.x, y: endRotation.y, z: endRotation.z}, 200)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();

				}

				this.gltf.pressingDown = () => {

					const t2 = new TWEEN.Tween(this.gltf.scale)
					.to({z: 0, x: 0, y: 0}, 2000)
					// .yoyo(true)
					// .repeat(Infinity)
					.easing(TWEEN.Easing.Quadratic.In)
					.start();

					new TWEEN.Tween(this.gltf.rotation)
					.to({x: 200}, 2000)
					.easing(TWEEN.Easing.Quadratic.In)
					.start();


				}


				this.scene.add(this.gltf);

				window.addEventListener('mousemove', this.onMouseMove, false);
				window.addEventListener('click', this.onClick);
				//
				window.addEventListener("mousedown", this.pressingDown, false);
				window.addEventListener("mouseup", this.notPressingDown, false);
				window.addEventListener("mouseleave", this.notPressingDown, false);
				document.addEventListener("mouseleave", () => this.gltf.resetLookAt(), false);

				window.addEventListener("touchstart", this.pressingDown, false);
				window.addEventListener("touchend", this.notPressingDown, false);

				this.setState({loaded: true})

		})

		// var planeGeometry = new THREE.PlaneBufferGeometry( 2000, 200, 32, 32 );
		// var planeMaterial = new THREE.MeshStandardMaterial( { side: THREE.DoubleSide} )
		// var plane = new THREE.Mesh( planeGeometry, planeMaterial );
		// plane.receiveShadow = true;
		// plane.position.z = 200;
		// this.scene.add( plane );
		//
		// var spotLight = new THREE.SpotLight( 0xffffff );
		// spotLight.position.set( 0, 350, 0 );
		// spotLight.castShadow = true;
		//
		//
		// spotLight.shadow.mapSize.width = 1024;
		// spotLight.shadow.mapSize.height = 1024;
		//
		// spotLight.shadow.mapSize.width = 512;  // default
		// spotLight.shadow.mapSize.height = 512; // default
		// spotLight.shadow.camera.near = 0.5;       // default
		// spotLight.shadow.camera.far = 500
		// spotLight.power = 100;
		// spotLight.target = plane;
		//
		// this.scene.add( spotLight );

	}

	componentDidMount() {
		this.sceneSetup();
		this.loadFrank();
		this.lightSetup();
		this.textSetup();


		this.box = new THREE.Mesh(new THREE.BoxBufferGeometry(), new THREE.MeshNormalMaterial());
		this.box.position.set(0, 0, 150);
		this.box.geometry.translate(0, 0, 0.5);
		this.box.scale.set(10, 10, 30);
		this.box.up = new THREE.Vector3(0, 0, 1);
		// this.box.rotation.setFromVector3(new THREE.Vector3( 0, -Math.PI * 123, 0));
		// this.scene.add(this.box);

		this.box.rotation.setFromVector3(new THREE.Vector3(Math.PI / -2, 0, 0));

		const rotation = this.box.quaternion.clone();

		this.plane = new THREE.Plane(new THREE.Vector3(0, 0, 1), -200);
		this.plane.normal.set(0, 0, 1).applyQuaternion(rotation);
		this.helper = new THREE.PlaneHelper( this.plane, 1000, 0xffff00 );
		// this.scene.add(this.helper);
		this.pointOfIntersection = new THREE.Vector3();


		// window.addEventListener('wheel', this.handleScroll, true);



		this.start()
	}


	pressingDown = () => {
		if(!this.state.distorted){
			this.setState({pressed: true, checkItersect: false});
		}
	}

	notPressingDown = () => {
		if(!this.state.distorted){
			if(this.counter < 150){
				this.objectsToShake.forEach(x => x.resetQuick());
				new TWEEN.Tween(this.uniforms.amplitude)
					.to({value: 0}, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
				this.gltf.reset();
			}
			this.counter = 0;
			this.setState({distorted: false, pressed: false, checkItersect: true})
		}
	}

	reset = () => {
		this.counter = 0;
		this.setState({rotate: false})
		this.objectsToShake.forEach(x => x.reset());
		this.gltf.reset();
		new TWEEN.Tween(this.uniforms.amplitude)
			.to({value: 0}, 2000)
			.easing(TWEEN.Easing.Quadratic.Out)
			.onComplete(() => {
				this.setState({rotate: false, pressed: false, distorted: false, checkItersect: true, showReset: false})
			})
			.start();
	}

	distort = () => {
		new TWEEN.Tween(this.uniforms.amplitude)
			.to({value: 12}, 1000)
			.easing(TWEEN.Easing.Quadratic.Out)
			.onComplete(() => this.setState({showReset: true}))
			.start();
		this.gltf.scale.set(0.00001, 0.00001, 0.00001);
	}

	checkTesselation = () => {
		if(this.state.pressed){
			if(this.counter < 150){
				this.counter += 1;
				this.objectsToShake.forEach(x => x.shake());
				this.gltf.shake();
			}else{
				this.setState({distorted: true, rotate: true, pressed: false});
				this.distort();
			}
		}
		if(this.state.rotate){
			this.objectsToShake.forEach(x => x.rotate());
		}
	}

	animate = () => {
		if(this.state.checkItersect){
			this.checkIfIntersects();
		}
		this.checkTesselation();
		this.frameId = window.requestAnimationFrame(this.animate);
		this.renderer.render( this.scene, this.camera );
		TWEEN.update()
	}


	render() {
		return (
			<>
				<div ref={(mount) => { this.mount = mount }} className="canvas"></div>
				{this.state.showReset ?
					<div className="resetBtn" onClick={() => this.reset()}>
						<button type="button" className="btn btn-dark">Reset</button>
					</div>
				:
					<div className="hold">tap and hold</div>
			}
				{this.state.loaded ? null : <div className="pulsating-circle"></div> }
			</>
		)
	}
}

export default withRouter(ThreeTable);
