import React, { Component } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { MapControls } from 'three/examples/jsm/controls/MapControls.js';


class ThreeBackground extends Component {
	state = {

	}

	start = () => {
		if (!this.frameId) {
			this.frameId = requestAnimationFrame(this.animate)
		}
	}


	componentDidMount() {
		this.scene = new THREE.Scene();
		// this.scene.background = new THREE.Color(0xf4f4f4);
		this.scene.background = new THREE.Color(0x00000);
		this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );
		this.camera.position.set(0, 0, 0);
		this.renderer = new THREE.WebGLRenderer();
		this.renderer.setSize( window.innerWidth, window.innerHeight );
		// this.controls = new OrbitControls(this.camera, this.renderer.domElement);
		this.controls = new MapControls( this.camera, this.renderer.domElement );
		this.renderer.shadowMap.enabled = true;
		this.renderer.shadowMap.type = THREE.BasicShadowMap;

		this.mount.appendChild(this.renderer.domElement);


		this.scene.add( new THREE.AmbientLight( 0x111122 ) );

		const sphereGeometry = new THREE.SphereGeometry(0.3, 32, 32);
		const sphereMaterial = new THREE.MeshBasicMaterial({color: 0xffff00});
		const asphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
		this.scene.add(asphere);

		const boxGeometry = new THREE.BoxBufferGeometry( 10, 60, 10 );
		const boxMaterial = new THREE.MeshPhongMaterial( {
					color: 0xa0adaf,
					shininess: 10,
					specular: 0x111111,
					side: THREE.BackSide
				} );
		const box = new THREE.Mesh( boxGeometry, boxMaterial );
		box.receiveShadow = true;
		box.position.y = 10;
		this.scene.add( box );

		var intensity = 1.5;
		var pointLight = new THREE.PointLight( 0xffffff, intensity, 20 );
		pointLight.castShadow = true;
		pointLight.shadow.camera.near = 1;
		pointLight.shadow.camera.far = 60;
		pointLight.shadow.bias = - 0.005; // reduces self-shadowing on double-sided objects
		var geometry = new THREE.SphereBufferGeometry( 0.3, 12, 6 );
		var material = new THREE.MeshBasicMaterial( { color: 0xffffff } );
		material.color.multiplyScalar( intensity );
		var sphere = new THREE.Mesh( geometry, material );
		pointLight.add( sphere );
		var canvas = document.createElement( 'canvas' );
		canvas.width = 2;
		canvas.height = 2;
		var context = canvas.getContext( '2d' );
		context.fillStyle = 'white';
		context.fillRect( 0, 1, 2, 1 );
		var texture = new THREE.CanvasTexture( canvas );
		texture.magFilter = THREE.NearestFilter;
		texture.wrapT = THREE.RepeatWrapping;
		texture.wrapS = THREE.RepeatWrapping;
		texture.repeat.set( 1, 3.5 );
		var geometry = new THREE.SphereBufferGeometry( 2, 32, 8 );
		var material = new THREE.MeshPhongMaterial( {
			side: THREE.DoubleSide,
			alphaMap: texture,
			alphaTest: 0.5
		} );
		var sphere = new THREE.Mesh( geometry, material );
		sphere.castShadow = true;
		sphere.receiveShadow = true;
		pointLight.add( sphere );
		// custom distance material
		var distanceMaterial = new THREE.MeshDistanceMaterial( {
			alphaMap: material.alphaMap,
			alphaTest: material.alphaTest
		} );
		sphere.customDistanceMaterial = distanceMaterial;
		this.scene.add(pointLight)



		this.camera.position.z = 5;
		this.start()
	}

	animate = () => {
		this.frameId = window.requestAnimationFrame(this.animate);
		this.renderer.render( this.scene, this.camera );
	}


	render() {
		return (
			<div ref={(mount) => { this.mount = mount }} className="canvas">
			</div>
		)
	}
}

export default ThreeBackground;
