import React, { Component } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { MapControls } from 'three/examples/jsm/controls/MapControls.js';
import { TessellateModifier } from 'three/examples/jsm/modifiers/TessellateModifier.js';
const TWEEN = require('@tweenjs/tween.js');

const Square = () => {
	var geometry = new THREE.BoxBufferGeometry( 1, 1, 1 );
	var material = new THREE.MeshBasicMaterial( {color: 0x00ff00} );
	var cube = new THREE.Mesh( geometry, material );
	return cube;
}

const Text = (text, uniforms, font) => {
	let geometry, mesh;

	geometry = new THREE.TextGeometry(text, {
		font: font,
		size: 10,
		height: 0.01,
		curveSegments: 4,
		bevelThickness: 0.1,
		bevelSize: 0.001,
		bevelEnabled: true
	});
	geometry.center();


	const tessellateModifier = new TessellateModifier( 1 );

	// how many pieces is the string split into
	for (let i=0; i<3; i++) {
		tessellateModifier.modify(geometry);
	}

	geometry = new THREE.BufferGeometry().fromGeometry(geometry);

	const numFaces = geometry.attributes.position.count / 3;
	const displacement = new Float32Array( numFaces * 6 * 3 );
	for ( var f = 0; f < numFaces; f ++ ) {
		const index = 9 * f;
		const d = 111 * ( 0.5 - Math.random() );
		for ( var i = 0; i < 1111; i++ ) {
			displacement[ index + ( 3 * i ) ] = d;
			displacement[ index + ( 3 * i ) + 1 ] = d;
			displacement[ index + ( 3 * i ) + 2 ] = d;
		}
	}

	geometry.addAttribute('displacement', new THREE.BufferAttribute(displacement, 3));

	const shaderMaterial = new THREE.ShaderMaterial({
		uniforms: uniforms,
		vertexShader: document.getElementById('vertexshader').textContent,
		fragmentShader: document.getElementById('fragmentshader').textContent
	});

	mesh = new THREE.Mesh(geometry, shaderMaterial);

	return mesh;
}

class TesselationTest extends Component {
	state = {
		rotate: false,
		pressed: false,
		distorted: false
	}

	start = () => {
		if (!this.frameId) {
			this.frameId = requestAnimationFrame(this.animate)
		}
	}

	containsObject = (obj) => {
		for(const x in this.objectsIncluded) {
			if(this.objectsIncluded.hasOwnProperty(x) && this.objectsIncluded[x] === obj){
				return true;
			}
		}
		return false;
	}

	animateVector3 = (vectorToAnimate, target, options) => {

			options = options || {};
			// get targets from options or set to defaults
			var to = target || THREE.Vector3(),
					easing = options.easing || TWEEN.Easing.Quadratic.In,
					duration = options.duration || 2000;
			// create the tween
			var that = this;
			var tweenVector3 = new TWEEN.Tween(vectorToAnimate)
					.to({ x: to.x, y: to.y, z: to.z, }, duration)
					.easing(easing)
					.onUpdate(function(d) {
							if(options.update){
									options.update(d);
							}
							if(options.runOnUpdate){
								options.runOnUpdate()
							}
					 })
					.onComplete(function(){
						if(options.callback) options.callback();
					});
			// start the tween
			tweenVector3.start();
			// return the tween in case we want to manipulate it later on
			return tweenVector3;
	}

	sceneSetup = () => {
		this.scene = new THREE.Scene();
		this.scene.background = new THREE.Color( 0xffffff );
		this.renderer = new THREE.WebGLRenderer( { antialias: true } );
		this.renderer.setPixelRatio( window.devicePixelRatio );
		this.renderer.setSize( window.innerWidth, window.innerHeight );

		this.mount.appendChild(this.renderer.domElement);

		this.camera = new THREE.PerspectiveCamera( 90, window.innerWidth / window.innerHeight, 1, 10000 );
		this.camera.position.set( 20, 20, 40 );

		this.controls = new MapControls( this.camera, this.renderer.domElement );
		// this.controls.enableZoom = false;
		this.controls.target = new THREE.Vector3(0, 0, 0);
		this.controls.update();

		window.addEventListener('resize', () => {
			this.renderer.setSize(window.innerWidth, window.innerHeight);
			this.camera.aspect = window.innerWidth / window.innerHeight;
			this.camera.updateProjectionMatrix();
		});
	}


	componentDidMount() {
		this.sceneSetup();

		// var geometry = new THREE.BoxBufferGeometry( 1, 1, 1 );
		// var material = new THREE.MeshBasicMaterial( {color: 0x00ff00} );
		// var cube = new THREE.Mesh( geometry, material );
		// this.scene.add( cube );
		this.scene.add(Square())

		this.uniforms = {
			amplitude: { value: 0.0 }
		};

		// Text('heheh', this.uniforms).then((t) => {
		// 	console.log(t)
		// 	this.scene.add(t)
		// })

		new THREE.FontLoader().load('fonts/Modern Sans_Regular.json', font => {
			this.mesh = Text("Hi, I'm a full-stack developer.", this.uniforms, font);
			this.scene.add(this.mesh)
		});





		// window.addEventListener('click', this.distort)

		this.counter = 0;

		window.addEventListener("mousedown", this.pressingDown, false);
		window.addEventListener("mouseup", this.notPressingDown, false);
		window.addEventListener("mouseleave", this.notPressingDown, false);

		window.addEventListener("touchstart", this.pressingDown, false);
		window.addEventListener("touchend", this.notPressingDown, false);


		this.start()
	}


	pressingDown = () => {
		console.log(this.mesh)
		if(!this.state.distorted){
			this.setState({pressed: true});
		}
	}

	shake = (mesh) => {
		mesh.position.set(Math.random()*10-5, Math.random()*10-5, Math.random()*10-5);
		mesh.material.uniforms.amplitude.value += 0.01;
	}

	notPressingDown = () => {
		if(!this.state.distorted){
			if(this.counter < 100){
				new TWEEN.Tween(this.uniforms.amplitude)
					.to({value: 0}, 500)
					.easing(TWEEN.Easing.Quadratic.Out)
					.start();
			}
			this.counter = 0;
			this.setState({distorted: false, pressed: false})
		}
	}

	distort = () => {
		const t1 = new TWEEN.Tween(this.uniforms.amplitude)
			.delay(10000)
			.to({value: 0}, 5000)
			.yoyo(true)
			.repeat(Infinity)
			.easing(TWEEN.Easing.Quadratic.Out)
		new TWEEN.Tween(this.uniforms.amplitude)
			.to({value: 3}, 1000)
			.easing(TWEEN.Easing.Quadratic.Out)
			.chain(t1)
			.start();
	}

	checkTesselation = () => {
		if(this.state.pressed){
			if(this.counter < 100){
				this.counter += 1;
				this.shake(this.mesh)
			}else{
				this.setState({distorted: true, rotate: true, pressed: false});
				this.distort();
			}
		}
		if(this.state.rotate){
			this.mesh.rotation.y += 0.001;
		}
	}


	animate = () => {
		this.checkTesselation();
		this.frameId = window.requestAnimationFrame(this.animate);
		this.renderer.render( this.scene, this.camera );
		TWEEN.update()
	}


	render() {
		return (
			<div ref={(mount) => { this.mount = mount }} className="canvas" >
			</div>
		)
	}
}

export default TesselationTest;
