import React, { Component } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { MapControls } from 'three/examples/jsm/controls/MapControls.js';
const TWEEN = require('@tweenjs/tween.js');


class WaveTest extends Component {
	state = {

	}

	start = () => {
		if (!this.frameId) {
			this.frameId = requestAnimationFrame(this.animate)
		}
	}

	containsObject = (obj) => {
		for(const x in this.objectsIncluded) {
			if(this.objectsIncluded.hasOwnProperty(x) && this.objectsIncluded[x] === obj){
				return true;
			}
		}
		return false;
	}

	animateVector3 = (vectorToAnimate, target, options) => {

			options = options || {};
			// get targets from options or set to defaults
			var to = target || THREE.Vector3(),
					easing = options.easing || TWEEN.Easing.Quadratic.In,
					duration = options.duration || 2000;
			// create the tween
			var that = this;
			var tweenVector3 = new TWEEN.Tween(vectorToAnimate)
					.to({ x: to.x, y: to.y, z: to.z, }, duration)
					.easing(easing)
					.onUpdate(function(d) {
							if(options.update){
									options.update(d);
							}
							if(options.runOnUpdate){
								options.runOnUpdate()
							}
					 })
					.onComplete(function(){
						if(options.callback) options.callback();
					});
			// start the tween
			tweenVector3.start();
			// return the tween in case we want to manipulate it later on
			return tweenVector3;
	}



	componentDidMount() {
		this.scene = new THREE.Scene();
		this.scene.background = new THREE.Color( 0xffffff );
		this.renderer = new THREE.WebGLRenderer( { antialias: true } );
		this.renderer.setPixelRatio( window.devicePixelRatio );
		this.renderer.setSize( window.innerWidth, window.innerHeight );

		this.mount.appendChild(this.renderer.domElement);

		this.camera = new THREE.PerspectiveCamera( 90, window.innerWidth / window.innerHeight, 1, 10000 );
		this.camera.position.set( 0, 100, 0 );

		this.controls = new MapControls( this.camera, this.renderer.domElement );
		// this.controls.enableZoom = false;
		this.controls.target = new THREE.Vector3(0, 0, 0);
		this.controls.update();

		window.addEventListener('resize', () => {
			this.renderer.setSize(window.innerWidth, window.innerHeight);
			this.camera.aspect = window.innerWidth / window.innerHeight;
			this.camera.updateProjectionMatrix();
		});


		var SEPARATION = 100, AMOUNTX = 50, AMOUNTY = 50;
		var container, stats;
		var camera, scene, renderer;
		var particles, count = 0;
		var mouseX = 0, mouseY = 0;
		var windowHalfX = window.innerWidth / 2;
		var windowHalfY = window.innerHeight / 2;


		var numParticles = AMOUNTX * AMOUNTY;
		var positions = new Float32Array( numParticles * 3 );
		var scales = new Float32Array( numParticles );
		var i = 0, j = 0;
		for ( var ix = 0; ix < AMOUNTX; ix ++ ) {
			for ( var iy = 0; iy < AMOUNTY; iy ++ ) {
				positions[ i ] = ix * SEPARATION - ( ( AMOUNTX * SEPARATION ) / 2 ); // x
				positions[ i + 1 ] = 0; // y
				positions[ i + 2 ] = iy * SEPARATION - ( ( AMOUNTY * SEPARATION ) / 2 ); // z
				scales[ j ] = 1;
				i += 3;
				j ++;
			}
		}
		var geometry = new THREE.BufferGeometry();
		geometry.addAttribute( 'position', new THREE.BufferAttribute( positions, 3 ) );
		geometry.addAttribute( 'scale', new THREE.BufferAttribute( scales, 1 ) );
		var material = new THREE.ShaderMaterial( {
			uniforms: {
				color: { value: new THREE.Color( 0x000000 ) },
			},
			vertexShader: document.getElementById( 'vertexshader' ).textContent,
			fragmentShader: document.getElementById( 'fragmentshader' ).textContent
		} );
		//
		this.particles = new THREE.Points( geometry, material );
		this.scene.add( this.particles );
		this.count = 1;




		this.start()
	}




	animate = () => {
		this.frameId = window.requestAnimationFrame(this.animate);


		var positions = this.particles.geometry.attributes.position.array;
		var scales = this.particles.geometry.attributes.scale.array;
		var i = 0, j = 0;
		for ( var ix = 0; ix < 50; ix ++ ) {
			for ( var iy = 0; iy < 50; iy ++ ) {
				positions[ i + 1 ] = ( Math.sin( ( ix + this.count ) * 0.3 ) * 50 ) +
								( Math.sin( ( iy + this.count ) * 0.5 ) * 50 );
				scales[ j ] = ( Math.sin( ( ix + this.count ) * 0.3 ) + 1 ) * 8 +
								( Math.sin( ( iy + this.count ) * 0.5 ) + 1 ) * 8;
				i += 3;
				j ++;
			}
		}
		this.particles.geometry.attributes.position.needsUpdate = true;
		this.particles.geometry.attributes.scale.needsUpdate = true;
		this.renderer.render( this.scene, this.camera );
		this.count += 0.1;


		TWEEN.update()
	}


	render() {
		return (
			<div ref={(mount) => { this.mount = mount }} className="canvas" >
			</div>
		)
	}
}

export default WaveTest;
