import React, { Component, useState } from 'react';
import { Redirect } from 'react-router-dom';
import { NavLink, Link, withRouter } from 'react-router-dom';
import {useSpring, animated, useTransition} from 'react-spring'

const Technology = (state) => {
	const {tech} = state;
	const props = useSpring({
	  transform: 'translate3d(0,0px,0)',
		from : {transform: 'translate3d(0,-40px,0)'}
	})
	return (
		<animated.div style={props}>
			<div className="techdiv">
				{tech.map((item, i) =>
					<div className="tech" key={i}>
						{item}
					</div>
				)}
			</div>
		</animated.div>
	)
}

const GoToWebsite = (state) => {
	const {link, github} = state;
	const props = useSpring({
		transform: 'translate3d(0,0px,0)',
		from : {transform: 'translate3d(0,40px,0)'}
	})
	return (
		<animated.div style={props}>
			<a href={link} style={{color: 'white'}}>Go to website</a>
			{github ? <a href={github}><img src="img/GitHub-Mark-Light-32px.png" style={{margin: 'auto', marginTop: '10px'}}/></a> : null}
		</animated.div>
	)
}

const More = (state) => {
	const {nextPath} = state;
	const props = useSpring({
		transform: 'translate3d(0,0px,0)',
		from : {transform: 'translate3d(0,40px,0)'}
	})
	return (
		<animated.div style={props}>
			<div className="more">
				<a onClick={nextPath} style={{color: 'white'}}>more...</a>
			</div>
		</animated.div>
	)
}

class Work extends Component {
	state = {
		show: false,
		imgIndex: 2,
		width: window.innerWidth
	}

	delay = async(delayInms) => {
		return new Promise(resolve  => {
			setTimeout(() => {
				resolve(2);
			}, delayInms);
		})
	}

	componentDidMount(){
		this.changeIndex();
	}

	changeIndex = async() => {
		await this.delay(4000);
		this.setState({imgIndex: this.state.imgIndex+1})
		this.changeIndex();
	}

	nextPath = (path) => {
		this.props.history.push(path);
	}

	render() {
		return (
			<div className="mt-5 img-container">
				{this.state.width > 1024 ?
					<>
						<img src={this.props.img[this.state.imgIndex%this.props.img.length]} style={{maxWidth: '100%', border: '1px solid grey', width: '800px', boxShadow: '0 10px 20px rgba(0,0,0,0.15)'}} />

						<div className="overlay" onMouseOver={() => this.setState({show: true})}>
							<span>
								{this.state.show ? <Technology tech={this.props.tech} /> : null}
								{this.state.show ? <GoToWebsite link={this.props.link} github={this.props.github} /> : null}
								{this.state.show ? <More nextPath={() => this.nextPath('/'+this.props.id)} /> : null}
							</span>
						</div>
					</>
					:
					<img src={this.props.img[this.state.imgIndex%this.props.img.length]} style={{maxWidth: '100%', border: '1px solid grey', width: '800px', boxShadow: '0 10px 20px rgba(0,0,0,0.15)'}} onClick={() => this.nextPath('/'+this.props.id)} />
				}
			</div>
		)
	}
}

export default withRouter(Work);
